import {
  Backdrop,
  Button,
  CircularProgress,
  Drawer,
  Grid,
  IconButton,
  List,
  ListItemText,
  Modal,
  Paper,
  Select,
  Slider,
  Switch,
  Table,
  TableBody,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TextField,
  Tooltip,
  Typography,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import moment from 'moment';
import { formatBytes } from '../../../Utility/helpers';

